.selector {
  display: inline-flex;
  list-style: none;
}

.button {
  display: inline-flex;
  font-family: inherit;
  font-size: 18px;
  line-height: 25px;
  cursor: pointer;
  border: none;
  padding: 0 10px;
  color: #C2C2C2;
  background: none;
  text-decoration: none;
  border: 1px solid #162E41;
}

.active {
  composes: button;
  color: #FBFBFB;
  border: 1px solid #315F8F;
}
.active:focus, .active:hover, .active:checked, .active:active, .active:visited, .active:target {
  color: #FBFBFB;
  text-decoration: none;
}
