.wrapper {
  background-color: #162E41;
  padding: 10px 0;
  display: flex;
  align-items: center;
}

.flexWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.burger {
  margin-right: 30px;
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.burgerLine {
  width: 100%;
  height: 2px;
  background: #FBFBFB;
  border-radius: 50px;
}

.heightWrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.imgSupport {
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: all .3s ease;
  fill: #FBFBFB;
  margin-left: 40px;
}

.imgSupportOpen {
  composes: imgSupport;
  fill: #00A3DE;
  transform: rotate(-90deg);
}

.telsBlock:first-child {
  padding-top: 20px;
}

.telsBlock {
  padding-bottom: 20px;
  text-align: center;
}

.telsBlock:last-child {
  padding-bottom: 10px;
}

.service {
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #00A3DE;
  margin-bottom: 5px;
}

.serviceTel {
  font-size: 18px;
  line-height: 25px;
  color: #FBFBFB;
}

.smallText {
  font-size: 14px;
  line-height: 19px;
}

@media(min-width: 1200px) {
  .wrapper {
    min-height: 46px;
    padding: 0;
  }
}
