.wrapper {
  position: relative;
}

.textTitle {
  white-space: nowrap;
  font-family: 'Nunito';
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FBFBFB;
  padding: 10px 42px 10px 25px;
  display: block;
  transition: all 0.3s ease;
}

.textTitleActive {
  composes: textTitle;
  background: #315F8F;
}

.list {
  position: absolute;
  z-index: 999;
  top: 100%;
  left: 0;
  list-style: none;
  background: #DFE5EC;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: 'Open Sans';
  min-width: 100%;
}

.listItem {
  white-space: nowrap;
  padding: 10px 20px;
  transition: all 0.3s ease;
}

.listItem:hover {
  background: #315F8F;
}

.link {
  text-decoration: none;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
  transition: all 0.3s ease;
}
.link:hover {
  text-decoration: none;
  color: #000000;
}

.listItem:hover > .link {
  color: #FFFFFF;
}
