.middleWrapper {
  width: 1366px;
  height: 100%;
  margin: 0 auto;
}

.slideViberOffice {
  height: 410px;
  margin-bottom: 30px;
  width: 1366px;
  position: absolute;
  margin: 0 auto;
  left: 50%;
  transform: translate(-50%, 0);
}

.slideViber {
  width: 703px;
  height: 100%;
  clip-path: polygon(0% 0%, 100% 0%, calc(100% - 70px) 100%, 0% 100%);
  position: absolute;
  top: 0;
  left: 0;
}

.slide {
  margin: 30px 0;
  height: 165px;
  background: rgba(255, 255, 255, 0.6);
}

.viber {
  height: 185px;
  background: rgba(255, 255, 255, 0.6);
}

.office {
  width: 703px;
  height: 410px;
  background: rgba(26, 37, 43, 0.6);
  clip-path: polygon(70px 0%, 100% 0%, 100% 100%, 0% 100%);
  position: absolute;
  top: 0;
  right: 0;
}

.bottomThree {
  width: 1366px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 11px;
  position: absolute;
  bottom: 0;
  height: 112px;
  left: 50%;
  transform: translate(-50%, 0);
}

.bottomThreeChild {
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottomThreeChild:first-child {
  clip-path: polygon(0 0, 100% 0, calc(100% - 20px) 100%, 0 100%);
}

.bottomThreeChild:nth-child(2) {
  clip-path: polygon(20px 0, 100% 0, calc(100% - 20px) 100%, 0 100%);
}

.bottomThreeChild:last-child {
  clip-path: polygon(20px 0, 100% 0, 100% 100%, 0 100%);
}

.bottomThreeChildWrapper {
  max-width: 260px;
  display: flex;
  align-items: center;
  white-space: pre-line;
  position: relative;
}

.bottomThreeChildImg {
  display: block;
  width: 50px;
  height: 50px;
  margin-right: 20px;
}

.bottomThreeChildText {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #1E2930;
}
.bottomThreeChildLink {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
}
