.wrapper {
  background: #fff;
  height: 100%;
  font-family: 'Open Sans';
}

.listWrapper {
  overflow: hidden;
}

.form {
  padding-right: 35px;
  width: 100%;
  background: #214161;
  position: relative;
  margin-bottom: 5px;
}

.input {
  padding: 9px 10px;
  background: none;
  color: #FBFBFB;
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  display: block;
  width: 100%;
  border: none;
}

.searchImg {
  position: absolute;
  right: 10px;
  top: 50%;
  width: 29px;
  height: 29px;
  z-index: 999;
  transform: translate(0, -50%);
}

.list {
  list-style: none;
}

.listItem {
  background: #162E41;
  padding: 10px;
}

.listItem:not(:last-child) {
  margin-bottom: 5px;
}

.streetName {
  font-size: 14px;
  line-height: 19px;
  margin-bottom: 10px;
  color: #FBFBFB;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.placeholderImg {
  display: block;
  margin-right: 5px;
  width: 15px;
  height: 20px;
}

.crushName {
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  color: #FBFBFB;
  margin-bottom: 10px;
}

.dateWrapper {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 5px;
  color: #00A3DE;
}

.timeStart {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.timeEnd {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.timeDiagram {
  margin-bottom: 5px;
  height: 20px;
  background: #DEDFDF;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  clip-path: polygon(4px 0, 100% 0%, calc(100% - 4px) 100%, 0 100%);
}

.timePercent {
  font-size: 14px;
  line-height: 1;
  color: #000;
  z-index: 3;
}

.streetList {
  padding: 10px;
  list-style: none;
  font-size: 12px;
  line-height: 24px;
  color: #FBFBFB;
}

.noRezults {
  font-size: 16px;
  color: #FBFBFB;
  text-align: center;
  padding: 20px;
  text-transform: uppercase;
}

@media(min-width: 1200px) {
  .form {
    padding-right: 90px;
    position: absolute;
    width: 813.15px;
    top: 0;
    left: 0;
    z-index: 5;
    background: #162E41;
  }

  .input {
    padding: 8px 30px;
  }

  .searchImgWrapper {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 90px;
    z-index: 6;
    background: #214161;
  }

  .searchImg {
    position: unset;
    right: unset;
    top: unset;
    transform: unset;
    display: block;
  }

  .list {
    overflow-y: auto;
    height: 540px;
  }

  .streetName {
    font-size: 16px;
    line-height: 22px;
  }

  .placeholderImg {
    width: 22px;
    height: 30px;
  }

  .crushName {
    padding: 0 5px;
    margin-bottom: 10px;
    font-weight: 600;
  }

  .dateWrapper {
    padding: 0 10px;
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 0;
  }

  .timeDiagram {
    margin: 0 5px 10px 5px;
  }
}
