.list {
  display: flex;
  list-style: none;
}

.listItem {
  display: flex;
  align-items: center;
  position: relative;
}

.arrow {
  margin-left: 5px;
  transition: all 0.3s ease;
  position: absolute;
  right: 25px;
  top: 40%;
}

.listItem:hover>.arrow {
  transform: rotate(180deg);
}

.link {
  font-family: 'Nunito';
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FBFBFB;
  text-decoration: none;
  display: block;
  padding: 10px 25px;
  transition: all 0.3s ease;
}
.link:hover {
  color: #FBFBFB;
  text-decoration: none;
  background: #315F8F;
}
