.link {
  display: flex;
  font-family: "Nunito";
  text-decoration: none;
  color: #315F8F;
  align-items: center;
  white-space: nowrap;
}
.link:hover,
.link:active,
.link:checked {
  text-decoration: none;
  color: #315F8F;
}

.logo {
  display: block;
  width: 51px;
  height: auto;
  margin-right: 5px;
}

.logoTextWrapper {
  display: flex;
  flex-direction: column;
}

.logoTitle {
  font-weight: 600;
  font-size: 14px;
  line-height: 23px;
}

.logoName {
  font-weight: 600;
  font-size: 22px;
  line-height: 36px;
}
