.wrapper {
  width: 100%;
  margin-top: 20px;
}

.head {
  font-size: 24px;
  line-height: 31px;
  text-transform: uppercase;
  margin-bottom: 10px;
  color: #FBFBFB;
  text-align: center;
}

.userAvaWrapper {
  width: 60px;
  height: 60px;
  margin: 0 auto 10px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 2px solid #FBFBFB;
}

.userAva {
  display: block;
  width: 40px;
  height: 40px;
}

.userTextWrapper {
  display: flex;
  margin-bottom: 40px;
  flex-direction: column;
  align-items: center;
}

.userName {
  font-size: 14px;
  line-height: 19px;
  color: #FBFBFB;
  font-family: "Nunito";
  margin-bottom: 10px;
}

.userName:first-child {
  margin-bottom: 0;
}

.userMail {
  font-size: 14px;
  line-height: 19px;
  color: #FBFBFB;
}

.buttonsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
}

.homeLink {
  width: 100%;
  background: #315F8F;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
  color: #FBFBFB;
  transition: all 0.3s ease;
  padding: 8px 10px 8px 10px;
  text-decoration: none;
  display: block;
  text-align: center;
  margin-bottom: 20px;
}

.exit {
  width: 100%;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
  color: #FBFBFB;
  border: 1px solid #315F8F;
  transition: all 0.3s ease;
  padding: 8px 10px 8px 10px;
  text-decoration: none;
  background: none;
  cursor: pointer;
}

.exit:hover {
  background: #FBFBFB;
  color: #1E2930;
}

.homeLink:hover {
  background: #00A3DE;
}

@media (min-width: 1200px) {
  .buttonsWrapper {
    flex-direction: row;
  }

  .homeLink {
    margin-right: 20px;
    margin-bottom: 0;
  }

  .userTextWrapper {
    margin-bottom: 35px;
  }

  .userName {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 22px;
  }

  .userName:first-child {
    margin-bottom: 0;
  }

  .userMail {
    font-size: 16px;
    line-height: 22px;
  }
}
