.wrapper {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 1170px;
  height: 410px;
  z-index: 998;
  display: flex;
  justify-content: stretch;
}

.left {
  width: 100%;
  margin-top: 30px;
}

.office {
  width: 100%;
  padding: 30px 0 30px 150px;
}

.slider {
  width: 560px;
  height: 165px;
  margin-bottom: 30px;
  padding: 20px 0;
}

.slider>div {
  height: 100%;
}

.slider>div>div {
  height: 100%;
}

.viber {
  padding-right: 50px;
  height: 185px;
  padding: 20px 0;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.viberQrCode {
  margin-right: 20px;
  display: block;
}

.viberText {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: #1E2930;
  white-space: pre-line;
}

.viberEng {
  font-weight: 600;
  font-size: 24px;
  line-height: 39px;
  text-transform: uppercase;
  color: #1E2930;
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
}
.viberLogo {
  display: block;
  margin-left: 15px;
}
