.carousel {
  height: 100%;
}

.carousel .slider-wrapper {
  height: 100%;
}

.carousel .slider {
  height: 100%;
}

.carousel .slide {
  background: none;
}

.carousel .control-prev.control-arrow::before {
  border-right: none;
}

.carousel .control-next.control-arrow::before {
  border-left: none;
}

.carousel .control-prev {
  background-image: url('../../assets/img/icons/angle-arrow-left.svg') !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
}
.carousel .control-next {
  background-image: url('../../assets//img/icons/angle-arrow-right.svg') !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
}
