.carousel {
  height: 100%;
}

.carousel>li {
  background: none;
}

.imgSlide {
  display: none;
}

.element {
  display: flex;
  flex-direction: column;
  text-align: left;
  height: 100%;
  padding: 0 40px;
  position: relative;
}

.date {
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
  color: #1E2930;
  margin-bottom: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
}

.title {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 20px;
  line-height: 33px;
  text-transform: uppercase;
  color: #1E2930;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
  margin-bottom: 10px;
}

.description {
  font-size: 16px;
  line-height: 22px;
  color: #1E2930;
  font-style: italic;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
}

.link {
  position: absolute;
  width: calc(100% - 80px);
  height: 100%;
  top: 0;
  left: 40px;
  z-index: 999;
}
