.form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.controlWrapper {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
}

.checkBoxLabel {
  display: flex;
  align-items: center;
  color: #1E3139;
}

.checkBoxLabel input {
  margin-right: 10px;
}

.forgotPassword {
  text-decoration: none;
  color: #5C7C84;
}

.forgotPassword:hover {
  text-decoration: none;
  color: #5C7C84;
}

.button {
  background: #315F8F;
  font-size: 16px;
  line-height: 22px;
  padding: 8px 10px 8px 10px;
  width: 100%;
  text-align: center;
  border: none;
  color: #FBFBFB;
  text-transform: uppercase;
  cursor: pointer;
  transition: all .3s ease;
  margin-bottom: 20px;
}

.button:hover {
  background: #00A3DE;
}

.linkButton {
  width: 100%;
  display: block;
  font-size: 16px;
  line-height: 22px;
  padding: 8px 10px 8px 10px;
  color: #FBFBFB;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  border: 1px solid #315F8F;
  transition: all 0.3s ease;
}

.linkButton:hover {
  background: #FBFBFB;
  color: #1E2930;
  border: 1px solid #FBFBFB;
}

.buttonWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.help {
  font-size: 14px;
  line-height: 20px;
  color: #FBFBFB;
  text-decoration: none;
}

.help:hover {
  color: #FBFBFB;
  text-decoration: none;
}

.help:not(:last-child) {
  margin-bottom: 10px;
}

.errorWrapper {
  position: absolute;
  top: calc(100% + 11px);
  left: 0;
  background: #EBECEC;
  width: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  z-index: 9997;
  border: 1px solid #E73C3C;
}

.errorWrapper::before {
  display: block;
  content: '';
  width: 20px;
  height: 11px;
  background: #E73C3C;
  position: absolute;
  z-index: 9998;
  top: -11px;
  left: 20px;
  clip-path: polygon(50% 0, 100% 100%, 0 100%);
}

.errorWrapper::after {
  display: block;
  content: '';
  width: 18px;
  height: 10px;
  background: #EBECEC;
  position: absolute;
  z-index: 9999;
  top: -10px;
  left: 21px;
  clip-path: polygon(50% 0, 100% 100%, 0 100%);
}

.errorIcon {
  display: block;
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.errorText {
  font-size: 14px;
  line-height: 19px;
  color: #E73C3C;
}

@media(min-width: 1200px) {
  .controlWrapper {
    font-size: 16px;
    margin-bottom: 25px;
  }

  .checkBoxLabel input {
    margin-right: 15px;
  }

  .button {
    font-size: 18px;
    line-height: 25px;
    margin: 0 30px 0 0;
  }

  .buttonWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: stretch;
  }

  .linkButton {
    font-size: 18px;
    line-height: 25px;
  }

  .help {
    font-size: 16px;
    line-height: 22px;
  }
}
