@media(min-width: 1200px) {
  .overflow {
    overflow: hidden;
    position: relative;
    height: 552px;
  }
  .wrapper {
    background: url('../../assets/img/background/index.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/index.webp') 1x, url('../../assets/img/background/index.jpg') 1x);
    background-color: #2E545E;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(5px);
    position: absolute;
    height: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
  }
}
