.listItem {
  position: relative;
}

.date {
  font-family: 'Nunito';
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  padding: 7px 13px;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #315F8F;
}

.imgWrapper {
  height: 224px;
  width: 100%;
}

.img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.listItem:not(:last-child) {
  margin-bottom: 20px;
}

.textArticle {
  height: 122px;
  background: #DFE5EC;
  padding: 20px 10px;
  overflow: hidden;
}

.title {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #315F8F;
  height: 52px;
  overflow: hidden;
  margin-bottom: 8px;
}

.text {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  width: 100%;
}
.link {
  position: absolute;
  z-index: 999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
