.listItem {
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-template-rows: 140px;
  position: relative;
}

.imgWrapper {
  height: 100%;
  width: 200px;
  position: relative;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.darkBack {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
  top: 0;
  left: 0;
  transition: all 0.3s ease;
}

.textArticle {
  overflow: hidden;
  padding: 10px 20px 10px 20px;
  background: #DFE5EC;
  transition: all 0.3s ease;
}

.title {
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #315F8F;
  margin-bottom: 5px;
  overflow: hidden;
  padding-right: 90px;
  transition: all 0.3s ease;
  max-height: 78px;
}

.text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
  transition: all 0.3s ease;
}
.date {
  font-family: 'Nunito';
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px 10px;
  background: #315F8F;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FFFFFF;
  z-index: 2;
  transition: all 0.3s ease;
}
.linkArticle {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 4;
}

.line {
  position: absolute;
  z-index: 3;
  width: 78px;
  height: 6px;
  bottom: 0;
  left: 20px;
  background: #00A3DE;
  transition: all 0.3s ease;
}

.listItem:hover .darkBack {
  background: rgba(22, 46, 65, 0.8);
}

.listItem:hover .textArticle {
  background: #162E41;
}

.listItem:hover .title, .listItem:hover .text {
  color: #ffffff;
}
.listItem:hover .date {
  background: #00A3DE;
}

.listItem:hover .line {
  width: 530px;
}
