.listMob {
  margin-bottom: 20px;
  list-style: none;
}

.listDesk {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  margin-bottom: 80px;
}

.buttonMobWrapper {
  margin-bottom: 50px;
}

.newsName {
  font-family: "Nunito";
  margin-bottom: 20px;
  font-size: 26px;
  line-height: 40px;
  text-transform: uppercase;
  color: #315F8F;
  border-bottom: 1px solid #315F8F;
}

.linkMob {
  display: block;
  margin: 0 auto;
  padding: 9px;
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  color: #FBFBFB;
  background: #315F8F;
  text-decoration: none;
  text-align: center;
}

@media(min-width: 1200px) {
  .newsName {
    margin-bottom: 30px;
    position: relative;
    font-weight: 600;
    font-size: 36px;
    line-height: 59px;
    text-transform: uppercase;
    color: #315F8F;
    border-bottom: 1px solid #315F8F;
    padding-top: 80px;
  }

  .linkDesk {
    display: inline-block;
    position: absolute;
    bottom: -1px;
    right: 0;
    padding: 8px 45px;
    background: #315F8F;
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    color: #FBFBFB;
    text-decoration: none;
    transition: all 0.3s ease;
    z-index: 5;
  }

  .linkDesk:hover,
  .linkDesk:active,
  .linkDesk:focus {
    text-decoration: none;
    color: #FBFBFB;
    background: #00A3DE;
  }
}
