.footer {
  background: #162E41;
  padding-top: 10px;
}

.cyberBack {
  background: #1E2930;
}

.linkWrapper {
  border-bottom: 1px solid #214161;
  margin-bottom: 50px;
}

.homeLink {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding-bottom: 10px;
}

.logoNickImg {
  display: block;
  width: 51px;
  height: 101px;
  margin-right: 10px;
}

.titleNickMob {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
  color: #FBFBFB;
}

.footerInfoWrapper {
  padding-bottom: 50px;
}

.infoEl {
  padding: 10px 0 0 30px;
  position: relative;
}

.infoEl:not(:last-child) {
  margin-bottom: 50px;
}

.blocImg {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.infoElHead {
  font-family: 'Nunito';
  position: relative;
  z-index: 1;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 20px;
}

.infoElTextBlock {
  color: #C2C2C2;
}

.infoElText {
  white-space: pre-line;
  font-size: 14px;
  line-height: 20px;
}

.infoElTextBlock:not(:last-child) {
  margin-bottom: 10px;
}

.infoElText:not(:last-child) {
  margin-bottom: 5px;
}

.infoElTextBlockLong {
  color: #C2C2C2;
}

.infoElTextBlockLong:not(:last-child) {
  margin-bottom: 20px;
}

.infoElTextBlockFacebook {
  padding-top: 10px;
}

.infoElTextFacebook {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #C2C2C2;
}

.facebookLink {
  margin-left: 10px;
  display: flex;
  text-decoration: none;
}

.facebookIcon {
  display: block;
  width: 30px;
  height: 30px;
}

.infoElText a {
  text-decoration: none;
  color: #C2C2C2;
}

.infoElTextTel {
  composes: infoElText;
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
}

.textAddon {
  font-size: 14px;
  line-height: 20px;
  font-weight: normal;
}

.footerTable {
  border: none;
  font-size: 14px;
  line-height: 20px;
  color: #C2C2C2;
}

.footerTableTr:not(:last-child)>.footerTableTd {
  padding-bottom: 10px;
}

.footerTableTd:not(:last-child) {
  padding-right: 20px;
}

.cctWrapper {
  padding: 20px 0;
  font-size: 14px;
  line-height: 20px;
  color: #C2C2C2;
}

.left {
  margin-bottom: 20px;
  white-space: pre-line;
}

.right {
  display: flex;
  align-items: center;
}

.cctImgLink {
  display: flex;
  text-decoration: none;
  margin-right: 10px;
}

.cctLogo {
  display: block;
  width: 70px;
  height: 66px;
}

.cctName {
  text-decoration: none;
  text-transform: uppercase;
  color: #87C0CE;
}

.cctName:hover {
  text-decoration: none;
  color: #87C0CE;
}

.rightTextInner:first-child {
  margin-bottom: 5px;
}

@media(min-width: 1200px) {
  .footerInfoWrapper {
    padding-bottom: 30px;
  }

  .linkWrapper {
    margin-bottom: 30px;
  }

  .homeLink {
    display: inline-flex;
  }

  .logoNickImg {
    margin-right: 0;
  }

  .textHeadWrapper {
    display: flex;
    flex-direction: column;
    text-align: left;
  }

  .infoEl:not(:last-child) {
    margin-bottom: 0;
  }

  .titleNick {
    font-family: 'Nunito';
    font-weight: 600;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    color: #FBFBFB;
    display: flex;
  }

  .titleName {
    font-weight: 600;
    font-size: 24px;
    line-height: 39px;
    text-align: center;
    color: #00A3DE;
    display: flex;
  }

  .footerInfoWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 15px;
  }

  .infoElHead {
    font-size: 18px;
    line-height: 30px;
  }

  .infoElText {
    font-size: 16px;
    line-height: 24px;
  }

  .infoElTextBlockFacebook {
    padding-top: 20px;
  }

  .infoElTextFacebook {
    font-size: 18px;
    line-height: 30px;
  }

  .infoElTextTel {
    composes: infoElText;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
  }

  .textAddon {
    font-size: 16px;
    line-height: 24px;
    font-weight: normal;
  }

  .footerTable {
    font-size: 16px;
    line-height: 24px;
  }

  .footerTableTd:not(:last-child) {
    padding-right: 10px;
  }

  .cctWrapper {
    padding: 10px 0;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .left {
    margin-bottom: 0;
  }

  .rightTextInner:first-child {
    margin-bottom: 0;
  }
}
