.contentBlockWrapper {
  padding-bottom: 50px;
}

.viberLink {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #315F8F;
  font-family: 'Nunito';
}

.viberImg {
  display: block;
  margin-right: 10px;
  width: 100px;
  height: auto;
}

.viberText {
  width: 200px;
}

.blockWrapper {
  padding: 15px 20px 15px 65px;
  clip-path: polygon(14px 0, 100% 0, 100% 100%, 0 100%);
  background: #DFE5EC;
  border-right: 10px solid #315F8F;
}

.blockWrapper:not(:last-child) {
  margin-bottom: 10px;
}

.blockLink {
  font-family: 'Nunito';
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #315F8F;
  text-decoration: none;
}

.blockIcon {
  display: block;
  margin-right: 20px;
  width: 50px;
  height: 50px;
}
