.officeWrapper {
  margin-top: 20px;
  background: #162E41;
  padding: 20px 10px;
  min-height: 442px;
  margin-bottom: 20px;
}

.loggingHeader {
  font-weight: 600;
  font-size: 22px;
  line-height: 36px;
  text-transform: uppercase;
  color: #FBFBFB;
  font-family: 'Nunito';
  text-align: center;
  margin-bottom: 10px;
}

.radio {
  display: flex;
  width: 100%;
  justify-content: stretch;
  margin-bottom: 5px;
}

.radioLabel {
  width: 100%;
  cursor: pointer;
  position: relative;
  padding-bottom: 6px;
}

.officeName {
  width: 100%;
  display: flex;
  justify-content: center;
}

.radioButton {
  position: absolute;
  z-index: 5;
  opacity: 0;
}

.radioLabelActive {
  composes: radioLabel;
  border-bottom: 2px solid #315F8F;
  position: relative;
}

.radioLabelActive::after {
  display: block;
  content: '';
  width: 10px;
  height: 5px;
  background: #315F8F;
  position: absolute;
  bottom: -7px;
  left: 50%;
  transform: translate(-50%, 0);
  clip-path: polygon(0 0, 100% 0, 50% 100%)
}

.officeIcon {
  display: block;
  width: 30px;
  height: 30px;
}

@media(min-width: 1200px) {
  .officeWrapper {
    min-height: unset;
    height: 100%;
    margin-top: 0;
    background: none;
    padding: 0;
    margin-bottom: 0;
  }

  .loggingHeader {
    font-size: 24px;
    line-height: 39px;
    margin-bottom: 20px;
  }

  .radioLabel {
    padding-bottom: 3px;
  }

  .radioLabel:first-child {
    margin-right: 30px;
  }

  .officeName {
    font-family: "Nunito";
    justify-content: flex-start;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    color: #FBFBFB;
    white-space: nowrap;
  }

  .officeIcon {
    margin-right: 10px;
  }
}
