.wrapper {
  display: flex;
  justify-content: flex-start;
}

.wrapper .logoBlock,
.wrapper .telsBlock {
  width: 100%;
}

.wrapper .line {
  border-right: 1px solid #315F8F;
  transform: rotate(9.5deg);
  margin-right: 15px;
}

.link {
  display: flex;
  font-family: "Nunito";
  text-decoration: none;
  color: #315F8F;
  align-items: center;
  margin-right: 30px;
  white-space: nowrap;
}

.link:hover,
.link:active,
.link:checked {
  text-decoration: none;
  color: #315F8F;
}

.logo {
  display: block;
  width: 51px;
  height: auto;
  margin-right: 5px;
}

.logoTextWrapper {
  display: flex;
  flex-direction: column;
}

.logoTitle {
  font-weight: 600;
  font-size: 15px;
  line-height: 25px;
}

.logoName {
  font-weight: 600;
  font-size: 24px;
  line-height: 39px;
}

.telsBlock:not(:last-child) {
  margin-right: 20px;
}

.service {
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #315F8F;
  white-space: nowrap;
}

.serviceTel {
  font-size: 18px;
  line-height: 25px;
  color: #162E41;
}

.smallText {
  font-size: 14px;
  line-height: 19px;
}
