.close {
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
}
.closeLine {
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FBFBFB;
  transform: rotate(45deg);
  border-radius: 50px;
}
.closeLine:last-child {
  transform: rotate(-45deg);
}
