.panel {
  background-color: none !important;
  box-shadow: none !important;
  margin-bottom: none !important;
}
.panel::after, .panel::before {
  display: none !important;
}
.panelSummary {
  margin: 0 !important;
}
.link {
  font-size: 18px;
  color: #FBFBFB;
  text-decoration: none;
  transition: color .4s ease;
}
.innerList {
  list-style: none;
  text-transform: lowercase !important;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px 0;
}
.innerListItem:not(:last-child) {
  margin-bottom: 10px;
}
.innerList .link {
  display: block;
  line-height: 22px;
  font-size: 16px;
}
.innerList .link::first-letter {
  text-transform: uppercase !important;
}
[class~="Mui-expanded"] .link {
  color: #C2C2C2;
}
.textWrapper {
  display: flex;
  align-items: center;
  position: relative;
}
.arrow {
  margin-left: 15px;
  transform: rotate(90deg);
  fill: #FBFBFB;
  width: 15px;
  height: 15px;
  transition: all .3s ease;
  position: absolute;
  right: -30px;
}
[class~="Mui-expanded"] .arrow {
  fill: #00A3DE;
  transform: rotate(-90deg);
}
