.mapControlWrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-bottom: 50px;
}

.mapWrapper {
  height: 541px;
}

.header {
  font-family: 'Nunito';
  font-weight: 600;
  font-size: 26px;
  line-height: 40px;
  text-transform: uppercase;
  color: #315F8F;
  margin-bottom: 20px;
  border-bottom: 1px solid #315F8F;
}

@media(min-width: 1200px) {
  .mapControlWrapper {
    flex-direction: row;
    height: 540px;
    margin-bottom: 80px;
    position: relative;
  }

  .mapInfoWrapper {
    order: 2;
    width: 366px;
    height: 100%;
    background: #fff;
  }

  .mapWrapper {
    order: 1;
    height: 100%;
    width: 834px;
    padding-top: 38px;
  }

  .header {
    font-size: 36px;
    line-height: 59px;
    margin-bottom: 30px;
  }
}
